import React, { useState } from 'react';
import {
  Card,
  CardContent,
  Typography,
  Box,
  TextField,
  Button,
  Chip,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Paper,
  Divider,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Slider,
  Switch,
  FormControlLabel,
  Alert,
  CircularProgress,
} from '@mui/material';
import {
  Search,
  FilterList,
  Sort,
  Refresh,
  Download,
  Share,
  Bookmark,
  TrendingUp,
} from '@mui/icons-material';

interface SearchResult {
  id: string;
  title: string;
  content: string;
  category: string;
  date: string;
  score: number;
  tags: string[];
}

const VectorSearch: React.FC = () => {
  const [searchQuery, setSearchQuery] = useState('');
  const [searchResults, setSearchResults] = useState<SearchResult[]>([]);
  const [isSearching, setIsSearching] = useState(false);
  const [searchType, setSearchType] = useState('hybrid');
  const [similarityThreshold, setSimilarityThreshold] = useState(0.7);
  const [maxResults, setMaxResults] = useState(20);
  const [enableReranking, setEnableReranking] = useState(true);

  const handleSearch = async () => {
    if (!searchQuery.trim()) return;
    
    setIsSearching(true);
    try {
      const response = await fetch('/api/query', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          query: searchQuery,
          top_k: maxResults,
          similarity_threshold: similarityThreshold,
          filters: {},
          max_context_length: 4000,
        }),
      });
      
      if (response.ok) {
        const result = await response.json();
        if (result && result.results) {
          const formattedResults = result.results.map((item: any) => ({
            id: item.id || item.article_id || '',
            title: item.title || item.metadata?.title || ' ',
            content: item.content || item.text || '',
            category: item.category || item.metadata?.category || '',
            date: item.date || item.metadata?.date || '',
            score: item.score || item.similarity || 0,
            tags: item.tags || item.metadata?.tags || [],
          }));
          setSearchResults(formattedResults);
        } else {
          setSearchResults([]);
        }
      } else {
        setSearchResults([]);
      }
    } catch (error) {
      console.error(' :', error);
      setSearchResults([]);
    } finally {
      setIsSearching(false);
    }
  };

  const handleKeyPress = (event: React.KeyboardEvent) => {
    if (event.key === 'Enter') {
      handleSearch();
    }
  };

  const getScoreColor = (score: number) => {
    if (score >= 0.9) return 'success';
    if (score >= 0.7) return 'warning';
    return 'error';
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" gutterBottom sx={{ fontWeight: 600, mb: 3 }}>
         
      </Typography>

      {/*   */}
      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
            <Box sx={{ flex: 1 }}>
              <TextField
                fullWidth
                label=" "
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder=":  , AI ,  ..."
                InputProps={{
                  endAdornment: (
                    <IconButton onClick={handleSearch} disabled={isSearching}>
                      {isSearching ? <CircularProgress size={20} /> : <Search />}
                    </IconButton>
                  ),
                }}
              />
            </Box>
            <Button
              variant="contained"
              onClick={handleSearch}
              disabled={isSearching || !searchQuery.trim()}
              startIcon={<Search />}
              sx={{ height: '56px' }}
            >
              
            </Button>
          </Box>
        </CardContent>
      </Card>

      <Box sx={{ display: 'flex', gap: 3 }}>
        {/*   */}
        <Box sx={{ flex: '0 0 300px' }}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                 
              </Typography>
              
              <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel> </InputLabel>
                <Select
                  value={searchType}
                  onChange={(e) => setSearchType(e.target.value)}
                >
                  <MenuItem value="vector"> </MenuItem>
                  <MenuItem value="keyword"> </MenuItem>
                  <MenuItem value="hybrid"> </MenuItem>
                </Select>
              </FormControl>

              <Typography gutterBottom>
                 : {similarityThreshold}
              </Typography>
              <Slider
                value={similarityThreshold}
                onChange={(_, value) => setSimilarityThreshold(value as number)}
                min={0}
                max={1}
                step={0.1}
                marks={[
                  { value: 0, label: '0' },
                  { value: 0.5, label: '0.5' },
                  { value: 1, label: '1' },
                ]}
                sx={{ mb: 2 }}
              />

              <Typography gutterBottom>
                  : {maxResults}
              </Typography>
              <Slider
                value={maxResults}
                onChange={(_, value) => setMaxResults(value as number)}
                min={5}
                max={100}
                step={5}
                marks={[
                  { value: 5, label: '5' },
                  { value: 50, label: '50' },
                  { value: 100, label: '100' },
                ]}
                sx={{ mb: 2 }}
              />

              <FormControlLabel
                control={
                  <Switch
                    checked={enableReranking}
                    onChange={(e) => setEnableReranking(e.target.checked)}
                  />
                }
                label=" "
              />
            </CardContent>
          </Card>

          {/*   */}
          <Card sx={{ mt: 2 }}>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                 
              </Typography>
              <List dense>
                <ListItem>
                  <ListItemText primary=" " secondary="2 " />
                </ListItem>
                <ListItem>
                  <ListItemText primary="AI  " secondary="1 " />
                </ListItem>
                <ListItem>
                  <ListItemText primary=" " secondary="2 " />
                </ListItem>
              </List>
            </CardContent>
          </Card>
        </Box>

        {/*   */}
        <Box sx={{ flex: 1 }}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <Typography variant="h6">
                    ({searchResults.length})
                </Typography>
                <Box>
                  <IconButton>
                    <Sort />
                  </IconButton>
                  <IconButton>
                    <FilterList />
                  </IconButton>
                  <IconButton>
                    <Refresh />
                  </IconButton>
                </Box>
              </Box>

              {searchResults.length === 0 && !isSearching && (
                <Alert severity="info">
                      .
                </Alert>
              )}

              {isSearching && (
                <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
                  <CircularProgress />
                </Box>
              )}

              {searchResults.map((result, index) => (
                <Paper key={result.id} sx={{ p: 2, mb: 2, border: '1px solid #e0e0e0' }}>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 1 }}>
                    <Typography variant="h6" sx={{ fontWeight: 600, flex: 1 }}>
                      {result.title}
                    </Typography>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <Chip
                        label={`${(result.score * 100).toFixed(1)}%`}
                        color={getScoreColor(result.score) as any}
                        size="small"
                      />
                      <IconButton size="small">
                        <Bookmark />
                      </IconButton>
                      <IconButton size="small">
                        <Share />
                      </IconButton>
                    </Box>
                  </Box>

                  <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
                    {result.content}
                  </Typography>

                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
                    <Chip label={result.category} size="small" variant="outlined" />
                    <Typography variant="caption" color="text.secondary">
                      {result.date}
                    </Typography>
                  </Box>

                  <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                    {result.tags.map((tag) => (
                      <Chip key={tag} label={tag} size="small" variant="outlined" />
                    ))}
                  </Box>
                </Paper>
              ))}
            </CardContent>
          </Card>
        </Box>
      </Box>
    </Box>
  );
};

export default VectorSearch;